body{
    background-image: url("https://img.freepik.com/free-vector/gorgeous-clouds-background-with-blue-sky-design_1017-25501.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    user-select: none
}

#root{
    border: 2px solid;
    margin-top: 30px;
    width: 800px;
    margin-left: 300px;
    border-radius: 15px;
    box-shadow: 2px 2px 10px;
}

.search-input{
    width: 60%;
    padding: 13px;
    border: none;
    outline: none;
    font-size: 16px;
    margin-top: 30px;
    margin-bottom: 50px;
    margin-left: 100px;
    margin-right: 10px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.5);
    font-weight: 1000;
}

.search-button{
    background-color: #28a745;
    color: white;
    padding: 10px 15px;
    border: none;
    cursor: pointer;
    width: 150px;
    padding: 15px;
    border-radius: 5px;
}

.test{
    display: flex;
    justify-content: space-evenly;
    padding-top: 60px;
    font-weight: 1000;
    padding-bottom: 20px;
}

.rain-temp{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.temp{
    font-size: 150px;
    margin-left: 400px;
}

.rain{
    font-size: 50px;
    margin-left: 400px;
}

.test >div{
    height: 150px;
    width: 200px;
    text-align: center;
    box-shadow: 2px 2px 4px;
}

.test :hover{
    background-color: lightskyblue;
}

.error{
    margin-left: 250px;
}

.loading{
    margin-left: 290px;
}